import { ArrayHelper } from "./arrayHelper";

export class StringHelper {


    /** Function that count occurrences of a substring in a string;
     * @author Vitim.us https://gist.github.com/victornpb/7736865
     * @see http://stackoverflow.com/questions/4009756/how-to-count-string-occurrence-in-string/7924240#7924240
     */
    public static OccurenceCount(value: string, searchString: string, allowOverlapping?: boolean) {
        value += "";
        searchString += "";
        if (searchString.length <= 0) return (value.length + 1);

        var n = 0,
            pos = 0,
            step = allowOverlapping ? 1 : searchString.length;

        while (true) {
            pos = value.indexOf(searchString, pos);
            if (pos >= 0) {
                ++n;
                pos += step;
            } else break;
        }
        return n;
    }

    public static RepeatString(value: string, repeatCount: number) {
        return ArrayHelper.InitEmptyArray(repeatCount + 1).join(value);
    }

    public static TrimStart(str: string | undefined, removeString: string | undefined) {
        if (!str || !removeString) { return str; }

        while (true) {
            const index = str.indexOf(removeString);
            if (index != 0) { return str; }

            str = str.substring(removeString.length);
        }
    }

    public static endsWith(self: string, search: string, this_len?: number) {
        if (this_len === undefined || this_len > self.length) {
            this_len = self.length;
        }
        return self.substring(this_len - search.length, this_len) === search;
    }

    /**
     * Returns a random string
     * @param length Length of generated random string
     */
    static randomStringGenerator(length: Number) {
        var result = '';
        var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for (var i = 0; i < length; i++) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
    }

    static capitalizeString(input: string): string {
        ///<summary>Capitialize first letter of a string</summary>
        return input.charAt(0).toUpperCase() + input.slice(1);
    };

    /**
     * Change char in string 
     * @param str String that to be changed
     * @param index Index of string
     * @param chr Char 
     */
    static setCharAt(str: string, index: number, chr: string): string {
        if (index < 0) {
            return str;
        }
        if (index > str.length) {
            index = str.length - 1;
        }
        return str.substr(0, index) + chr + str.substr(index + 1);
    }
}